/* App.css */
.App {
    font-family: 'Arial', sans-serif;
    padding: 20px;
    background-color: #f4f4f4;
    max-width: 1200px;
    margin: 0 auto;
}

.icon {
    max-width: 150px;
}

.category-section {
    margin-bottom: 40px;
}

    .category-section h2 {
        font-size: 1.5rem;
        color: #333;
        margin-bottom: 20px;
    }

.puzzle-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
}

.puzzle-item {
    background-color: #fff;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease-in-out;
}

    .puzzle-item:hover {
        transform: scale(1.05);
    }

.thumbnail {
    width: 100%;
    height: 150px;
    object-fit: cover;
}

h3 {
    font-size: 1.25rem;
    margin: 10px;
}

p {
    font-size: 1rem;
    color: #666;
    margin: 10px;
}

a {
    text-decoration: none;
    color: inherit;
}

.social-icon {
    color: #BB023C;
    margin: 0px 20px;
    vertical-align: middle;
    font-size: 20px;
    font-weight: bold;
    display: inline-block;
}
    .social-icon:hover {
        opacity: 0.8;
    }